
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    box-shadow: 0 0 40px lightgrey;
    padding: 10px;
    height: 60px;
}

.header h1 {
    margin-right: 30px;
}

.burgerMenuIcon {
    margin-left: 30px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    background-image: url('burger.svg'); /* Replace with your actual icon */
    background-size: cover;
}