.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 280px;
    background-color: #fff;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);


}

.sidebar.open {
    transform: translateX(0);
}

.background {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);

}

.nav {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-left: 50px;
}

.nav a {
    margin: 15px 0;
    display: block;
    font-family: Montserrat;
    text-decoration: none;
    color: black;
    font-size: 22px;
    font-weight: 600;
}

.nav a:hover {

    color: #58ACFF

}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    position: absolute;
    right: 0;
    background: none;
    border: none;
    padding: 5px;
}
.close:hover{
    scale: 1.2;
    transition: scale 0.2s ease-in-out;
}
